<template>
  <div class="player-controls" :class="myclass">
    <div class="player-controls__item -favorite" :class="{ active : playerMode.currentTrack.favorited }" @click="favorite">
      <svg class="icon">
        <use xlink:href="#icon-heart-o"></use>
      </svg>
    </div>
    <a :href="playerMode.currentTrack.url" target="_blank" class="player-controls__item">
      <svg class="icon">
        <use xlink:href="#icon-link"></use>
      </svg>
    </a>
    <div class="player-controls__item" @click="prevTrack">
      <svg class="icon">
        <use xlink:href="#icon-prev"></use>
      </svg>
    </div>
    <div class="player-controls__item" @click="nextTrack">
      <svg class="icon">
        <use xlink:href="#icon-next"></use>
      </svg>
    </div>
    <div class="player-controls__item -xl js-play" @click="play">
      <svg class="icon">
        <use xlink:href="#icon-pause" v-if="playerMode.isTimerPlaying"></use>
        <use xlink:href="#icon-play" v-else></use>
      </svg>
    </div>

