<script>
import playerMode from "./player-mode.js";
window.lm = playerMode;
export default {
  components: {},
  data() {
    return {
      playerMode: playerMode
    };
  },
  computed: {
    myclass() {
      return this.playerMode.type == "min" ? "player-controls-min" : "";
    }
  },
  methods: {
    prevTrack() {
      this.playerMode.audio.subject.isReadyPlay = false;
      this.playerMode.setData({
        transitionName: "scale-in"
      });
      let { currentTrackIndex, tracks } = this.playerMode.getData();

      // this.isShowCover = false;
      if (currentTrackIndex > 0) {
        currentTrackIndex--;
      } else {
        currentTrackIndex = tracks.length - 1;
      }
      this.playerMode.setData({
        currentTrackIndex
      });
      this.playerMode.setCurrentTrack({
        currentTrack: tracks[currentTrackIndex]
      });
      this.playerMode.resetPlayer();
    },
    nextTrack() {
      this.playerMode.audio.subject.isReadyPlay = false;
      this.playerMode.setData({
        transitionName: "scale-out"
      });
      let { currentTrackIndex, tracks, audio } = this.playerMode.getData();
      audio.pause();
      if (currentTrackIndex < tracks.length - 1) {
        currentTrackIndex++;
      } else {
        currentTrackIndex = 0;
      }
      this.playerMode.setData({
        currentTrackIndex
      });
      this.playerMode.setCurrentTrack({
        currentTrack: tracks[currentTrackIndex]
      });
      this.playerMode.resetPlayer();
    },
    play() {
      let { audio, isTimerPlaying } = this.playerMode.getData();
      if (isTimerPlaying == false) {
        let ret = audio.play();
        this.playerMode.setData({
          isTimerPlaying: ret
        });
      } else {
        audio.pause();
        this.playerMode.setData({
          isTimerPlaying: false
        });
      }
    },
    favorite() {
      let { currentTrackIndex, tracks } = this.playerMode.getData();
      tracks[currentTrackIndex].favorited = !tracks[currentTrackIndex]
        .favorited;

      this.playerMode.setCurrentTrack({
        currentTrack: tracks[currentTrackIndex]
      });
    }
  },
  created() {}
};
</script>
